div.presentational-carecenter-header {
  display        : -webkit-flex;
  display        : flex;
  width          : 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : flex-end;
          align-items    : flex-end;
}
div.presentational-carecenter-header > div.buttons {
  min-width : 64px;
  text-align: right;
}
div.presentational-carecenter-header div.name {
  font-weight  : bold;
  border-bottom: 1px solid var(--thm-bgPD);
  margin-bottom: 8px;
}
div.presentational-carecenter-header div.prop {
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : center;
          align-items    : center;
  opacity        : .5;
  min-width      : 190px;
}
div.presentational-carecenter-header div.prop > div:last-child {
  font-weight: bold;
  font-family: monospace;
  margin     : 0 0 0 16px;
}
div.tfw-view-flex {
  display: -webkit-inline-flex;
  display: inline-flex;
  margin : 2px;
}
div.tfw-view-flex.wide {
  display: -webkit-flex;
  display: flex;
  width  : 100%;
  margin : 0;
}
div.tfw-view-flex.dir-row {
  -webkit-flex-direction: row;
          flex-direction: row;
}
div.tfw-view-flex.dir-row-reverse {
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
div.tfw-view-flex.dir-column {
  -webkit-flex-direction: column;
          flex-direction: column;
}
div.tfw-view-flex.dir-column-reverse {
  -webkit-flex-direction: column-reverse;
          flex-direction: column-reverse;
}
@media(orientation:landscape) {
  div.tfw-view-flex.dir-wide {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
  div.tfw-view-flex.dir-narrow {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
}
@media(orientation:portrait) {
  div.tfw-view-flex.dir-wide {
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  div.tfw-view-flex.dir-narrow {
    -webkit-flex-direction: row;
            flex-direction: row;
  }
}
div.tfw-view-flex > .grow0 {
  -webkit-flex-grow: 0;
          flex-grow: 0;
}
div.tfw-view-flex > .grow1 {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
div.tfw-view-flex > .grow2 {
  -webkit-flex-grow: 2;
          flex-grow: 2;
}
div.tfw-view-flex > .grow3 {
  -webkit-flex-grow: 3;
          flex-grow: 3;
}
div.tfw-view-flex > .grow4 {
  -webkit-flex-grow: 4;
          flex-grow: 4;
}
div.tfw-view-flex > .grow5 {
  -webkit-flex-grow: 5;
          flex-grow: 5;
}
div.tfw-view-flex > .shrink0 {
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}
div.tfw-view-flex > .shrink1 {
  -webkit-flex-shrink: 1;
          flex-shrink: 1;
}
div.tfw-view-flex > .shrink2 {
  -webkit-flex-shrink: 2;
          flex-shrink: 2;
}
div.tfw-view-flex > .shrink3 {
  -webkit-flex-shrink: 3;
          flex-shrink: 3;
}
div.tfw-view-flex > .shrink4 {
  -webkit-flex-shrink: 4;
          flex-shrink: 4;
}
div.tfw-view-flex > .shrink5 {
  -webkit-flex-shrink: 5;
          flex-shrink: 5;
}
div.tfw-view-input-date {
  display        : -webkit-inline-flex;
  display        : inline-flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items    : stretch;
          align-items    : stretch;
  margin         : 2px;
  transition     : all .2s;
}
div.tfw-view-input-date.wide {
  display: -webkit-flex;
  display: flex;
}
div.tfw-view-input-date label {
  font-size: 80%;
  padding  : 2px .25rem;
}
div.tfw-view-input-date div.error {
  font-size  : 80%;
  font-weight: bold;
  padding    : 2px .25rem;
}
div.tfw-view-input-date input {
  font-size  : inherit;
  font-family: inherit;
  border     : none;
  padding    : 0 .25rem;
  line-height: 2rem;
  transition : all .2s;
  -webkit-flex       : 0 0 auto;
          flex       : 0 0 auto;
  text-align : center;
}
div.tfw-view-input-date select {
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}
div.tfw-view-input-date input,
div.tfw-view-input-date select {
  border    : none;
  background: var(--thm-bg3);
}
div.tfw-view-input-date input:focus,
div.tfw-view-input-date select:focus {
  background: var(--thm-bgSL);
}
div.tfw-view-input-date > div.inputs {
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : row;
          flex-direction : row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : stretch;
          align-items    : stretch;
  padding        : 0;
  margin         : 0;
}
div.tfw-view-input-date input.day {
  max-width: 3em;
  min-width: 3em;
}
div.tfw-view-input-date input.year {
  max-width: 4em;
  min-width: 4em;
}
div.presentational-pie {}
div.presentational-stat {
  margin         : 8px;
  padding        : 0;
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : column;
          flex-direction : column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items    : stretch;
          align-items    : stretch;
}
div.presentational-stat > div {
  padding: .5em;
}
div.presentational-stat > div:first-child {
  max-height : 2em;
  line-height: 2em;
  padding    : 0 .5em;
}
div.presentational-stat div.bullet {
  display      : inline-block;
  border-radius: 100%;
  width        : .7em;
  height       : .7em;
  margin       : 0 .5em;
}
div.tfw-layout-sidemenu {
  position    : absolute;
  margin      : 0;
  padding     : 0;
  left        : 0;
  top         : 0;
  width       : 100%;
  height      : 100%;
  overflow    : hidden;
  --menu-width: 312px;
}
div.tfw-layout-sidemenu > * {
  position: absolute;
  top     : 0;
  margin  : 0;
  padding : 0;
}
div.tfw-layout-sidemenu > div.icon {
  left           : 0;
  top            : 0;
  width          : 32px;
  height         : 32px;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
  border-radius  : 0 0 8px 0;
}
div.tfw-layout-sidemenu > div.menu {
  left      : 0;
  bottom    : 0;
  width     : var(--menu-width);
  -webkit-transform : translateX(-100%);
          transform : translateX(-100%);
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
div.tfw-layout-sidemenu.show > div.menu {
  -webkit-transform : translateX(0);
          transform : translateX(0);
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
}
div.tfw-layout-sidemenu > div.menu > header {
  position       : absolute;
  left           : 0;
  top            : 0;
  right          : 0;
  height         : 32px;
  display        : -webkit-flex;
  display        : flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
  overflow       : hidden;
  padding        : 0 0 0 32px;
}
div.tfw-layout-sidemenu > div.menu > menu {
  position: absolute;
  left    : 0;
  top     : 32px;
  right   : 0;
  bottom  : 0;
  overflow: auto;
  display : block;
  padding : 8px;
}
div.tfw-layout-sidemenu > div.body {
  position  : absolute;
  left      : 0;
  top       : 0;
  right     : 0;
  bottom    : 0;
  transition: left .2s steps(1,jump-start);
}
div.tfw-layout-sidemenu.show > div.body {
  position  : absolute;
  left      : var(--menu-width);
  top       : 0;
  right     : 0;
  bottom    : 0;
  transition: left .2s steps(1,jump-end);
}
div.container-sidemenu.welcome {
  max-width: 480px;
}
div.App > div.body {
  padding        : 1rem;
  overflow       : auto;
  display        : -webkit-flex;
  display        : flex;
  -webkit-flex-direction : row;
          flex-direction : row;
  -webkit-flex-wrap      : wrap;
          flex-wrap      : wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items    : center;
          align-items    : center;
}
div.App > div.menu ul {
  list-style-type: none;
  margin         : 8px;
  padding        : 0;
}
div.App > div.menu li {
  margin: 0;
}
*.grey {
  opacity: .5;
}
